.contenedor {
  margin: 0 auto;
  width: 90%;
  max-width: 768px;
}

.invisible {
  display: none;
}

.link {
  text-decoration: none;
  color: #000000;
  font-family: "Quicksand", Arial;
  width: 100%;
}

.titulo {
  text-transform: uppercase;
}

section {
  margin-top: 5rem;
}

.contacto {
  margin-bottom: 5rem;
}

.tarjeta-contacto {
  padding: 3rem;
  -webkit-box-shadow: -9px 9px 33px -8px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -9px 9px 33px -8px rgba(0, 0, 0, 0.75);
  box-shadow: -9px 9px 33px -8px rgba(0, 0, 0, 0.75);
}
@media (min-width: 480px) {
  .tarjeta-contacto {
    display: grid;
    align-items: center;
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 768px) {
  .tarjeta-contacto {
    grid-template-columns: 1fr 2fr;
  }
}
.tarjeta-contacto img {
  width: 30rem;
  height: 30rem;
  object-fit: cover;
}
@media (min-width: 480px) {
  .tarjeta-contacto img {
    width: 15rem;
    height: 15rem;
  }
}
@media (min-width: 768px) {
  .tarjeta-contacto img {
    width: 20rem;
    height: 20rem;
  }
}

.info-contacto h3 {
  margin-bottom: 0.5rem;
}
.info-contacto .domicilio {
  margin-top: 0.5rem;
}

.tel {
  display: flex;
  align-items: center;
}
.tel a {
  margin-left: 1rem;
}

.footer {
  background-color: #06C1DF;
  color: #FFFFFF;
  text-align: center;
  padding: 1.3rem 0;
}
.footer a {
  color: #FFFFFF;
  font-weight: 700;
}

.contenedor-galeria {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
@media (min-width: 768px) {
  .contenedor-galeria {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
@media (min-width: 1024px) {
  .contenedor-galeria {
    grid-template-columns: repeat(4, 1fr);
  }
}

.header {
  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1;
}
@media (min-width: 768px) {
  .header {
    background-color: #FFFFFF;
  }
}

.main {
  margin-top: 8rem;
}

.hamburger {
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: #06C1DF;
}
@media (min-width: 768px) {
  .hamburger {
    display: none;
  }
}
.hamburger .hamburger-box {
  margin: 0 auto;
}

#navbar {
  padding: 1rem 0;
  background-color: #FFFFFF;
}
#navbar ul {
  margin: 0;
  padding: 0;
  text-align: center;
}
@media (min-width: 768px) {
  #navbar ul {
    display: flex;
    justify-content: space-between;
  }
}
#navbar li {
  width: 100%;
  list-style: none;
}
#navbar li:hover {
  background-color: #06C1DF;
  cursor: pointer;
}
#navbar li:hover a {
  color: #FFFFFF;
}
@media (min-width: 768px) {
  #navbar li.active {
    background-color: #FFFFFF;
    border-radius: 3rem;
  }
}

.tarjeta {
  -webkit-box-shadow: -6px 6px 14px -2px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -6px 6px 14px -2px rgba(0, 0, 0, 0.75);
  box-shadow: -6px 6px 14px -2px rgba(0, 0, 0, 0.75);
  background-color: #f1f1f1;
  margin-bottom: 5rem;
  padding: 3rem;
}
@media (min-width: 768px) {
  .tarjeta {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
.tarjeta h3 {
  margin: 0;
}
.tarjeta img {
  margin: 0 auto;
  display: block;
  width: 20rem;
  height: 20rem;
  object-fit: cover;
}
@media (min-width: 480px) {
  .tarjeta img {
    width: 30rem;
    height: 30rem;
    max-width: 70%;
  }
}
@media (min-width: 768px) {
  .tarjeta img {
    width: 50rem;
    height: auto;
    max-width: 80%;
  }
}

.informacion h4 {
  text-transform: uppercase;
  margin-bottom: 0;
}

.precio {
  font-weight: 700;
}

@media (min-width: 768px) {
  .presentacion {
    display: grid;
    grid-template-columns: 2fr 1fr;
  }
}
.presentacion img {
  max-width: 70%;
  display: block;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .presentacion img {
    max-width: 100%;
  }
}